<template>
  <v-card class="wrapper littleMargin">
    <div class="littleMargin">
      <v-row id="mossadHoursDetails">
        <v-col cols="12" md="2">
          <p>מוסד - {{_mossadInfo.mossadName}}</p>
        </v-col>
        <v-col cols="12" md="2">
          <p>שעות מאוישות - {{_mossadInfo.currHours}}</p>
        </v-col>
        <v-col cols="12" md="2">
          <p>יתרת שעות - {{_mossadInfo.maxHours - _mossadInfo.currHours}}</p>
        </v-col>
        <v-col cols="12" md="3">
          <p>כמות שעות מקסימלית - {{_mossadInfo.maxHours}}</p>
        </v-col>
        <v-col cols="12" md="2">
          <p>אחוז איוש - {{ getTwoDigits(_mossadInfo.currHours ,_mossadInfo.maxHours) }}%</p>
        </v-col>
      </v-row>
      <v-row id="serchEmployee">
        <v-col cols="12" md="8">
          <v-card-text>
            <v-autocomplete
              v-model="empId"
              :items="tzArray"
              color="indigo lighten-5"
              hide-selected
              :item-text="item => item.firstName +' '+ item.lastName + ' - ' + item.empId"
              item-value="empId"
              label="תעודת זהות"
              placeholder="חפש"
              @change="getEmployeeInfo()"
            ></v-autocomplete>
          </v-card-text>
        </v-col>
      </v-row>
      <v-row class="second" v-if="Object.keys(this.employeeInfo).length > 0">
        <v-col class="test" cols="12" md="2">
          <p>שם פרטי</p>
          {{ employeeInfo.firstName }}
        </v-col>
        <v-col class="test" cols="12" md="2">
          <p>שם משפחה</p>
          {{ employeeInfo.lastName }}
        </v-col>
        <v-col class="test" cols="12" md="1">
          <p>גיל</p>
          {{ formatDate }}
        </v-col>
        <v-col class="test" cols="12" md="1">
          <p>מין</p>
          {{ formatGender }}
        </v-col>
        <v-col class="test" cols="12" md="1">
          <p>משרת אם</p>
          {{ formatIsMother }}
        </v-col>
        <v-col class="test" cols="12" md="1">
          <p>שעות גיל</p>
          {{ ageHours }}
        </v-col>
        <v-col cols="12" md="4">
          <table id="detailsTable" v-if="Object.keys(this.existHours).length > 0">
            <thead>
              <th></th>
              <th>א</th>
              <th>ב</th>
              <th>ג</th>
              <th>ד</th>
              <th>ה</th>
              <th>ו</th>
              <th>סיכום</th>
            </thead>
            <tbody>
              <tr v-for="(row, index) in existHours" :key="index">
                <td>{{getRowType(row.type)}}</td>
                <td v-for="(day,index) in row.week" :key="index">{{ day }}</td>
                <td>{{ totalHours(row.week) }}</td>
              </tr>
            </tbody>
          </table>
        </v-col>
      </v-row>

      <v-row v-if="empId != null">
        <v-col cols="12" md="2">
          <v-select
            v-model="reformId"
            :items="reformTypes"
            item-text="name"
            item-value="reformId"
            label="בחר רפורמה"
            single-line
          ></v-select>
        </v-col>
      </v-row>
      <v-card v-if="employeeInfo != null && reformId != null">
        <v-card class="center">
          <weeklyHours :empId="empId" :reformType="reformId"></weeklyHours>
        </v-card>
      </v-card>
    </div>
  </v-card>
</template>

<script>
import axios from "axios";
import weeklyHours from "./weekly-hours.vue";
export default {
  name: "setEmployeesHours",
  components: {
    weeklyHours,
  },
  data() {
    return {
      search: null,
      reformId: null,
      empId: null,
      tzArray: [],
      employeeInfo: {},
      existHours: [],
      weeklyHoursComponents: [],
      reformTypes: [],
    };
  },
  created() {
    this.getAllTz();
    this.getReformTypes();
  },
  computed: {
    _mossadInfo() {
      return this.$store.state.mossadInfo;
    },
    formatDate() {
      if (this.employeeInfo.birthDate != undefined) {
        var birthDate = new Date(this.employeeInfo.birthDate);
        var currDate = new Date();
        return currDate.getFullYear() - birthDate.getFullYear();
      } else {
        return undefined;
      }
    },
    formatGender() {
      if (this.employeeInfo.gender != undefined) {
        if (this.employeeInfo.gender === "M") {
          return "זכר";
        } else {
          return "נקבה";
        }
      }
      return undefined;
    },
    formatIsMother() {
      if (this.employeeInfo.mother === true) {
        return "כן";
      } else {
        return "לא";
      }
    },
    ageHours() {
      if (this.employeeInfo.birthDate === undefined) {
        return null;
      }
      var birthDate = new Date(this.employeeInfo.birthDate);
      var currDate = new Date();

      var age = currDate.getFullYear() - birthDate.getFullYear();
      if (age < 50) {
        return 0;
      } else if (age > 55) {
        return 4;
      } else {
        return 2;
      }
    },
  },
  methods: {
    totalHours(week) {
      if (week != null) {
        return week.reduce((acc, item) => parseInt(acc) + parseInt(item), 0);
      } else {
        return 0;
      }
    },
    getRowType(type) {
      if (type === 1) {
        return "במוסד";
      }
      return "בכל המוסדות";
    },
    getAllTz() {
      axios
        .get("/employees/all")
        .then((response) => {
          this.tzArray = response.data;
        })
        .catch((error) =>
          this.$store.dispatch("displayErrorMessage", {
            error,
          })
        );
    },
    getEmployeeInfo() {
      axios
        .get("/employees/byId", {
          params: {
            empId: this.empId,
          },
        })
        .then((response) => {
          this.employeeInfo = response.data;
        })
        .catch((error) =>
          this.$store.dispatch("displayErrorMessage", {
            error,
          })
        );

      this.getAllExistHours();
    },
    getAllExistHours() {
      this.existHours = [];
      axios
        .get("/teacherEmploymentDetails/weekSum", {
          params: {
            empId: this.empId,
          },
        })
        .then((response) => {
          this.existHours.push({ type: 1, week: response.data });
        })
        .catch((error) =>
          this.$store.dispatch("displayErrorMessage", {
            error,
          })
        );
      axios
        .get("/teacherEmploymentDetails/weekSumPerMossad", {
          params: {
            empId: this.empId,
            mossadId: this.$store.state.mossadId,
          },
        })
        .then((response) => {
          this.existHours.push({ type: 2, week: response.data });
        });
    },
    getReformTypes() {
      axios
        .get("/reformTypes/relevant")
        .then((response) => {
          this.reformTypes = response.data;
        })
        .catch((error) =>
          this.$store.dispatch("displayErrorMessage", {
            error,
          })
        );
    },
    navigateToHirepage() {
      this.$router.push("/HireEmp");
    },
    getTwoDigits(number, number2) {
      return parseFloat((number / number2) * 100).toFixed(2);
    },
  },
};
</script>

<style scoped>
/* .test {
  min-width: 40px;
  max-width: 220px;
} */
p {
  font-weight: bold;
}
.wrapper {
  /* border: 1px solid black; */
  overflow: hidden; /* will contain if #first is longer than #second */
}
.littleMargin {
  margin-right: 3%;
  margin-left: 3%;
}
/* .first {
  width: 300px;
  float: right; 
  margin: 5px;
}
.second {
  margin: 5px;
  overflow: hidden; 
} */

#serchEmployee {
  max-width: 400px;
}

.divider {
  width: 98%;
  overflow: hidden;
  margin-top: 30px;
}
table,
tr,
th,
td {
  border: 1px solid black;
  border-collapse: collapse;
}
tbody {
  display: table-row-group;
  vertical-align: inherit;
  border-block: inherit;
}
thead {
  display: table-header-group;
  vertical-align: inherit;
  border-color: inherit;
}
th {
  font-weight: bold;
}
td,
th {
  margin: 1px;
  padding: 1px;
  min-width: 25px;
  max-width: 100px;
  text-align: center;
}
.center {
  text-align: center;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
}
#detailsTable {
  text-align: center;
  justify-content: center;
  align-items: center;
  margin-left: 15%;
  margin-right: auto;
}
</style>
